import http from '../http'
const httpx = (url, type, params, exConfig, exData) => {
    // let sign = process.env.NODE_ENV === 'production' ? '/core' : '/oauth'
    // let sign = '/core'
    let sign = '/oauth'
    switch (type.toUpperCase()) {
        case 'GET':
            return http.get(sign + url, params, exConfig, exData)
            break
        case 'POST':
            return http.post(sign + url, params, exConfig, exData)
            break
        case 'PUT':
            return http.put(sign + url, params, exConfig, exData)
            break
        case 'DELETE':
            return http.delete(sign + url, params)
            break
    }
}

export default {
    getToken: data => httpx('/connect/token', 'POST', data, ["form", "noToken", "customError"]),
    getVerifyCode: data => httpx('/api/Home/GetVerifyCode', 'GET', data, ["noToken"]),
    connectToken: data => httpx('/connect/token', 'POST', data, ["form", "noToken"]),

    // 调度
    QuartzManage: data => httpx('/api/Quartz/Manage', 'GET', data),
    QuartzDelete: data => httpx('/api/Quartz/Delete', 'Delete', data)
}