<template>
    <div class="wrap">
        <div class="list-line" v-if="!diynew">
            <div class="list-line-label" v-if="!diy">
                {{ name }}
                <slot name="label" v-if="!name"></slot>
            </div>
            <div class="list-line-content" v-if="!diy">
                <slot />
            </div>
            <div v-if="diy" class="list-line-diy">
                <slot name="diy"></slot>
            </div>
        </div>
        <div class="listcard shadow" @click="toPage()" v-if="diynew">
            <div class="show"><img class="showImg" :src="item[defaultProp.img]" /></div>
            <div class="right">
                <div class="title hidden-two">{{ item[defaultProp.title] }}</div>
                <div class="time-wrap">
                    <i class="iconfont icon-status-leave"></i>
                    {{ item.CreateTime }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ListLine",
    props: {
        name: {
            type: String,
        },
        // 自定义
        diy: {
            type: Boolean,
            default: () => false,
        },
        diynew: {
            type: Boolean,
            default: () => false,
        },
        item: {},
        defaultProp: {
            type: Object,
            default: () => {
                return {
                    img: "img",
                    title: "title"
                }
            }
        },
        toPageUrl: {
            type: String,
            default: "/modular/oa/NewsDetails"
        }
    },
    methods: {
        toPage() {
            this.$router.push({
                path: this.toPageUrl,
                query: {
                    Id: this.item.Id,
                    src: this.item[this.defaultProp.img],
                },
            });
        },
    },
    mounted() { },
};
</script>

<style lang="scss" scoped>
.list-line {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    border-bottom: 1px dashed #eee;
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;

    .list-line-label {
        width: 120px;
        font-weight: 600;
        color: #3d464a;
    }

    .list-line-content {
        width: calc(100% - 120px);
        text-align: right;
        overflow: auto;
    }

    .list-line-diy {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
    }
}
.listcard {
    width: calc(100% - 30px);
    display: flex;
    justify-content: space-between;
    height: 208rpx;
    padding: 9px;
    margin: 10px 15px;
    .show {
        width: 100px;
        height: 80px;

        .showImg {
            width: 100%;
            height: 100%;
            border-radius: 4px;
        }
    }
    .right {
        width: 65%;
        margin-left: 30rpx;
        line-height: 30rpx;

        .title {
            line-height: 52rpx;
            font-size: 17px;
            color: #303133;
            font-weight: 600;
            margin-bottom: 6px;

            text {
                font-weight: 600;
                font-size: 32rpx;
            }
        }

        .time-wrap {
            display: flex;
            line-height: 30px;
            color: #666;
            font-size: 15px;

            i {
                margin-right: 6px;
                font-size: 14px;
            }
        }
    }
}
</style>