import http from '../http'
const httpx = (url, type, params, exConfig, exData) => {
    // let ModuleId = JSON.parse(localStorage.getItem("appStatus"))[0]
    let ModuleId = "5c4edc3a-b094-4656-894a-faff28b684a1"
    if (url.includes('mId') || url.includes('ModuleId')) url += ModuleId
    switch (type.toUpperCase()) {
        case 'GET':
            return http.get(`/core` + url, params, exConfig, exData);
            break;
        case 'POST':
            exConfig = exConfig ? exConfig.includes("exParam") ? exConfig : exConfig.concat(["exParam"]) : ["exParam"]
            // exData ? exData.ModuleId = ModuleId : exData = { ModuleId }
            params ? params.ModuleId = ModuleId : params = {
                ModuleId
            }
            return http.post(`/core` + url, params, exConfig, exData);
            break;
        case 'PUT':
            return http.put(`/core` + url, params, exConfig, exData);
            break;
        case 'DELETE':
            return http.delete(`/core` + url, params, exConfig, exData);
            break;
    }
}
