import http from '../http'
const httpx = (url, type, params, exConfig, exData) => {
    if (url.includes('mId')) url += JSON.parse(localStorage.getItem("appStatus"))[0]
    switch (type.toUpperCase()) {
        case 'GET':
            return http.get(`/bi` + url, params, exConfig, exData);
            break;
        case 'POST':
            return http.post(`/bi` + url, params, exConfig, exData);
            break;
        case 'PUT':
            return http.put(`/bi` + url, params, exConfig, exData);
            break;
        case 'DELETE':
            return http.delete(`/bi` + url, params);
            break;
    }
}
