<template>
    <div class="main-page">
        <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit" @down="downCallback" :style="{top:top,bottom:bottom}" id="mescroll">
            <slot></slot>
        </mescroll-vue>
    </div>
</template>
			
<script>
import MescrollVue from 'mescroll.js/mescroll.vue'

export default {
    name: "ScrollView",
    props: {
        top: {
            type: String,
            default: "50px"
        },
        bottom: {
            type: String,
            default: "0px"
        },
    },
    components: {
        MescrollVue
    },
    data() {
        return {
            mescroll: null,
            mescrollDown: {},
            mescrollUp: {
                callback: this.upCallback,
                page: {
                    num: 0,
                    size: 10
                },
                htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
                noMoreSize: 5,
                toTop: {
                    src: "/static/img/mescroll-totop.png",
                    offset: 1000
                },
                empty: {
                    warpId: "mescroll",
                    icon: "/static/img/mescroll-empty.png",
                    tip: ""
                }
            }
        }
    },

    methods: {
        mescrollInit(mescroll) {
            this.mescroll = mescroll
        },

        upCallback(page, mescroll) {
            this.$emit("upFun", [page, mescroll])
        },

        down() {
            this.mescroll.triggerDownScroll()
        },
        downCallback(Instance) {
            this.$emit("down", Instance)
        },
        up() {
            this.mescroll.triggerUpScroll()
        },
        reUp() {
            // 下拉页数从1开始，基本放弃了down
            this.mescroll.resetUpScroll([null, true, false])
        }
    }
}
</script>
			
<style scoped>
/*通过fixed固定mescroll的高度*/
.mescroll {
    position: fixed;
    height: auto;
}
</style>