import Vue from 'vue'
import App from './App.vue'
import router from './router'

Vue.config.productionTip = false

import packageJson from "../package.json"
Vue.prototype.web_version = packageJson.version

import Utils from './utils/utils'
Vue.prototype.$u = Utils

import http from "./api/api"
Vue.prototype._api = http

import moment from 'moment' // 引入文件   YYYY-MM-DD HH:mm:ss
Vue.prototype.$time = moment

import mixin from "./mixins"
Vue.mixin(mixin)

import guid from 'guid'
Vue.prototype.$guid = guid

// import '../public/static/font/font/iconfont.css'
// import './assets/css/index.scss'
// import './assets/css/theme.scss'

import '../public/font/iconfont.css'
import '../public/css/index.scss'
import "../public/css/theme.scss"

import * as echarts from 'echarts'
// import echarts from 'echarts';
Vue.prototype.$echarts = echarts

import Vant from 'vant';
import 'vant/lib/index.css';
Vue.use(Vant);

import {
  Lazyload
} from 'vant';
Vue.use(Lazyload);

// import Vconsole from 'vconsole';

import publicComponent from "./components/index"
Vue.use(publicComponent)

window.temFun = () => {}

// 获取配置文件
fetch("/static/config.json").then(data => {
  data.json().then(res => {
    Vue.prototype.$config = res
    document.title = res.siteTitle
    if (res.Vconsole) new Vconsole()
  })
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')