export default {
    name: 'RenderItem',
    functional: true,
    props: {
        render: Function,
        info: Object
    },
    render: (h, ctx) => {
        return ctx.props.render(h, ctx.props.info)
    }

    /*
        1:普通文本
        2:数值
        3:日期
        5:固定值下拉
        6:单选
        7:基础资料
        9:基础资料多选
        11:基础资料树形单选
        13:富文本
    */
}