import { ImagePreview } from 'vant';
let mixins = {
  data() {
    return {}
  },

  methods: {
    // 初始化
    init() {
      Object.assign(this.$data, this.$options.data())
    },
    // 预览图片
    preImage(url) {
      ImagePreview({
        images: [
          url
        ]
      });
    }
  }
}
export default mixins