import http from '../http'
const httpx = (url, type, params, exConfig, exData) => {
    if (url.includes('mId')) url += JSON.parse(localStorage.getItem("appStatus"))[0]
    switch (type.toUpperCase()) {
        case 'GET':
            return http.get(url, params, exConfig, exData);
            break;
        case 'POST':
            return http.post(url, params, exConfig, exData);
            break;
        case 'PUT':
            return http.put(url, params, exConfig, exData);
            break;
        case 'DELETE':
            return http.delete(url, params);
            break;
    }
}
export default {
    customApi: (url, type, data) => httpx(url, type ? type : "GET", {...data,PageSize:1000})
}