import Vue from 'vue'
import { Toast } from 'vant';
import { Dialog } from 'vant';

Vue.use(Toast);
export default {
  msg(msg,icon) {
    Toast({
      message: msg,
      icon,
      className:"van-Toast"
    })
  },

  confirm(msg, cancelTxt, confirmTxt,title,showCancelButton) {
    return Dialog.confirm({
      title: title,
      message: msg,
      showCancelButton: showCancelButton?showCancelButton:true,
      cancelButtonText: cancelTxt,
      confirmButtonText: confirmTxt
    })
  },

  clear() {
    localStorage.removeItem("token")
    sessionStorage.removeItem("token")
    localStorage.removeItem("refresh_token")
    localStorage.removeItem("webMyInfo")
    localStorage.removeItem("menuList")
    localStorage.removeItem("buttons")
    localStorage.removeItem("cpcInfo")
    localStorage.removeItem("ccylInfo")
    localStorage.removeItem("disInfo")
    localStorage.removeItem("labourUnionInfo")
    localStorage.removeItem("admList")
    localStorage.removeItem("sonMo")
    localStorage.removeItem("hiddenAside")
    localStorage.removeItem("needChangePassword")
    localStorage.removeItem("pageName")
    localStorage.removeItem("cpcList")
    localStorage.removeItem("luTree")
    localStorage.removeItem("luList")
    localStorage.removeItem("ccylList")
    localStorage.removeItem("curComName")
    localStorage.removeItem("myInfo")
    localStorage.removeItem("menu")
    // localStorage.removeItem("appStatus")
  },

  // 准确判断数据类型 [IE]  String | Undefined | Array | Object | Boolean | Number | Null | Function
  // 参考地址：https://blog.csdn.net/qq_41800366/article/details/114624177
  getType(variable) {
    return Object.prototype.toString.call(variable).replace(/^\[object (\w+)\]$/, '$1')

  },

  // Json 转 Url [IE]
  jsonToUrl(json, appointFieldArr, ignoreFieldArr) {
    if (this.getType(json) !== "Object" || JSON.stringify(json) == "{}") return ""
    let newJson = JSON.parse(JSON.stringify(json))
    // 如果不指定字段则全部转换，如果指定了字段则无视忽视字段
    if (this.getType(appointFieldArr) == "Array" && appointFieldArr.length) {
      // IE 不支持for...of forEach
      let temObj = {}
      for (let i = 0; i < appointFieldArr.length; i++) {
        temObj[appointFieldArr[i]] = newJson[appointFieldArr[i]]
      }
      newJson = temObj
    } else {
      if (this.getType(ignoreFieldArr) == "Array" && ignoreFieldArr.length) {
        for (let key in newJson) {
          if (ignoreFieldArr.includes(key)) delete newJson[key]
        }
      }
    }
    try {
      return new URLSearchParams(newJson).toString()
    } catch (error) {
      let urlStr = ""
      for (let key in newJson) {
        urlStr += key + "=" + newJson[key] + "&"
      }
      return urlStr.substr(0, urlStr.length - 1)
    }
  },

  // Url/href 转 Json [IE]
  urlToJson(url) {
    if (this.getType(url) !== "String" || !url) return {}

    // 转换多余的 ? 云之家可能出现这种问题
    let temArr = url.split("?")
    if (temArr.length > 2) url = temArr.join("&").replace("&", "?")

    let reg_url = /^[^\?]+\?([\w\W]+)$/,
      reg_para = /([^&=]+)=([\w\W]*?)(&|$|#)/g,
      arr_url = reg_url.exec(url),
      ret = {}
    if (arr_url && arr_url[1]) {
      let str_para = arr_url[1],
        result
      while ((result = reg_para.exec(str_para)) != null) {
        ret[result[1]] = result[2]
      }
    }
    return ret
  },

  // href 中提取某一个参数 [IE]
  hrefQueryString(name) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i")
    var r = window.location.search.substr(1).match(reg)
    if (r != null) return unescape(r[2])
    return null
  },

  // 获取当前页面缩放比例
  detectZoom() {
    var ratio = 0,
      screen = window.screen,
      ua = navigator.userAgent.toLowerCase()
    if (window.devicePixelRatio !== undefined) {
      ratio = window.devicePixelRatio
    } else if (~ua.indexOf('msie')) {
      if (screen.deviceXDPI && screen.logicalXDPI) {
        ratio = screen.deviceXDPI / screen.logicalXDPI
      }
    } else if (window.outerWidth !== undefined && window.innerWidth !== undefined) {
      ratio = window.outerWidth / window.innerWidth
    }
    if (ratio) {
      ratio = Math.round(ratio * 100)
    }
    return ratio
  },

  // 当前浏览器是否是IE浏览器
  isIE() {
    var userAgent = navigator.userAgent //取得浏览器的userAgent字符串  
    var isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1 //判断是否IE<11浏览器  
    var isEdge = userAgent.indexOf("Edge") > -1 && !isIE //判断是否IE的Edge浏览器  
    var isIE11 = userAgent.indexOf('Trident') > -1 && userAgent.indexOf("rv:11.0") > -1
    if (isIE) {
      var reIE = new RegExp("MSIE (\\d+\\.\\d+)")
      reIE.test(userAgent)
      var fIEVersion = parseFloat(RegExp["$1"])
      if (fIEVersion == 7) {
        return true
      } else if (fIEVersion == 8) {
        return true
      } else if (fIEVersion == 9) {
        return true
      } else if (fIEVersion == 10) {
        return true
      } else {
        return true //IE版本<=7
      }
    } else if (isEdge) {
      return false
    } else if (isIE11) {
      return true //IE11  
    } else {
      return false //不是ie浏览器
    }
  },

  // Json数组转树 [IE×]
  arraytoTree(arr) {
    if (this.getType(arr) !== "Array" || !arr) return []
    let temArr = JSON.parse(JSON.stringify(arr))
    let resultList = []
    let sourceMap = {}
    temArr.forEach(item => {
      item.level = 0
      sourceMap[item.Id] = item
    })
    temArr.forEach(item => {
      let parent = sourceMap[item.ParentId]
      if (parent) {
        item.level = Number(parent.level) + 1
        item.Path = `${parent.Path}/${item.Id}`;
        (parent.children || (parent.children = [])).push(item)
      } else {
        item.level = 0
        item.Path = item.Id
        resultList.push(item)
      }
    })
    return resultList
  },

  // 树形转Json数组
  treeToArray(tree) {
    if (this.getType(tree) !== "Array" || !tree) return []
    let sourceTree = JSON.parse(JSON.stringify(tree))
    return sourceTree.reduce((pre, cur) => {
      pre = pre.concat(cur).concat(cur.children && cur.children.length > 0 ? this.treeToArray(cur.children) : [])
      delete cur.children
      return pre
    }, [])
  },

  // 获取当前应用主题
  getAppTheme() {
    let appStatus = JSON.parse(localStorage.getItem("appStatus"))
    let modularsInfo = JSON.parse(localStorage.getItem("config")).modularsInfo
    let color = ""
    switch (modularsInfo[appStatus[1]].theme) {
      case "blue":
        color = "#409eff";
        break;
      case "red":
        color = "#dc2d2d";
        break;
    }
    return color
  },

  // 表单验证
  checkForm(form, rules, checkNumber0, checkNumberNegative) {
    return new Promise((resolve, reject) => {
      for (let key in rules) {
        let value = form[key]
        let errMsg = ""
        switch (this.getType(value)) {
          case "String":
            if (value.trim() == "") errMsg = rules[key]
            break;
          case "Array":
            if (value.length == 0) errMsg = rules[key]
            break;
          case "Object":
            if (JSON.stringify(value) == "{}") errMsg = rules[key]
            break;
          case "Number":
            if (checkNumber0 && value == 0) {
              errMsg = "不能输入数字 0 ！";
              break;
            }
            if (checkNumberNegative && value < 0) {
              errMsg = "不能输入负数 ！";
              break;
            }
            break;
          default:
            if (!value) errMsg = rules[key]
            break;
        }
        if (errMsg) return this.msg(errMsg, 'error')
      }
      resolve()
    })
  },

  // 数组中的元素是否包含某些相同的键值对
  hIncludes(arr, item, filedList) {
    let index = -1
    if (this.getType(arr) !== "Array" || this.getType(item) !== "Object") return flag
    if (filedList) {
      index = arr.findIndex(iter => {
        let same = true
        filedList.forEach(it => {
          if (iter[it] !== item[it]) same = false
        })
        return same
      })
    } else {
      index = arr.findIndex(iter => {
        let same = true
        for (let key in item) {
          if (item[key] !== iter[key]) {
            same = false
          }
        }
        return same
      })
    }
    return index
  },

  // 对比两个数组，返回较老数组减少的元素，返回较老数组新增的元素
  contrastArr(oldArr, newArr, keyStr) {
    let obj = {
      ReducedEle: [],
      NewEle: []
    }
    if (this.getType(oldArr) !== "Array" || !oldArr || this.getType(newArr) !== "Array" || !newArr || !keyStr) return obj
    obj.ReducedEle = oldArr.filter(item => newArr.findIndex(it => item[keyStr] == it[keyStr]) == -1)
    obj.NewEle = newArr.filter(item => oldArr.findIndex(it => item[keyStr] == it[keyStr]) == -1)
    return obj
  },

  // 百度富文本编辑框配置
  ueditor() {
    return {
      serverUrl: "/FMS/api/UEditor",
      initialFrameHeight: 400,
      // UEDITOR_HOME_URL: process.env.NODE_ENV === 'production' ? 'http://h.kdcloud.net.cn/UEditor/' : 'http://192.168.1.99:7777/UEditor/',
      UEDITOR_HOME_URL: `/UEditor/`,
    }
  },

  // 上传附件显示进度条
  showProgress(num) {
    let maskEle = document.getElementById("mask")
    maskEle.style.display = "flex"
    maskEle.innerHTML = `<div id="h-Progress">
      <div class="h-Progress-title">上传进度 ${num}%</div>
      <div class="h-Progress-content">
        <div class="h-Progress-content-b">
          <div class="h-Progress-p" style="width:${num}%"></div>
        </div>
      </div>
    </div>`
  },

  // 隐藏进度条
  hideProgress() {
    let maskEle = document.getElementById("mask")
    maskEle.style.display = "none"
  },

  // 获取应用主题
  getAppTheme() {
    let appStatus = this.getItem('appStatus')
    let modularsInfo = JSON.parse(localStorage.getItem("config")).modularsInfo
    let color = ""
    switch (modularsInfo[appStatus[1]].theme) {
      case "blue":
        color = "#409eff";
        break;
      case "red":
        color = "#dc2d2d";
        break;
    }
    return color
  },

  // 上传附件
  selectFiles(fun) {
    const input = document.createElement("input")
    input.setAttribute("type", "file")
    input.setAttribute("name", "fujian")
    input.click()
    input.onchange = function () {
      fun && fun(this.files)
    }
    input.remove()
  },

  // 分析excel
  analysisExcel(file) {
    return new Promise((resolve, reject) => {
      var reader = new FileReader()
      let fileExcel = {}
      reader.onload = async (e) => {
        fileExcel.oldFile = e.target.result
        var wb = XLSX.read(fileExcel.oldFile, {
          type: "binary"
        })
        var wb_sheetname = wb.SheetNames[0]
        const wb_sheet = wb.Sheets[wb_sheetname]
        let fromTo = wb_sheet['!ref']
        let selectFileData = XLSX.utils.sheet_to_json(wb_sheet)
        if (!selectFileData.length) {
          this.msg(`上传的文件数据为空！`, "error")
          return reject([])
        }

        return resolve(selectFileData)
      }
      reader.readAsBinaryString(file)
    })
  },

  // 判断清空无效字段数据
  clearInvalidField(field) {
    let InvalidStrList = ["null", "undefined", "0001-01-01T00:00:00"]
    if (InvalidStrList.includes(field)) return null
  },

  // render
  renderHtml(config) {
    let newConfig = []
    for (let item of config) {
        item.render = (h, info) => {
            return (
                <el-input v-model={info[item.FieldId]} placeholder={item.Placeholder} />
            )
        }
        newConfig.push(item)
    }
    return newConfig
  },

  // 批量下载
  download(fileList){
    this.confirm("请确认批量下载？").then(() => {
      fileList.forEach(function (item, index) {
          setTimeout(() => {
              let a = document.createElement('a'); // 创建a标签
              let e = document.createEvent('MouseEvents'); // 创建鼠标事件对象
              e.initEvent('click', false, false); // 初始化事件对象
              a.href = item.src; // 设置下载地址
              a.download = item.fname; // 设置下载文件名
              a.dispatchEvent(e);
          }, 100 * index)
      })
    }).catch(() => { })
  },

  setItem(name, data) {
		if (this.getType(data) === "String") {
			localStorage.setItem(name, data)
		} else {
			localStorage.setItem(name, JSON.stringify(data))
		}
	},

	getItem(name) {
		return localStorage.getItem(name)
	},

  clearStore(){
    localStorage.clear()
  }
}