import http from '../http'
const httpx = (url, type, params, exConfig, exData) => {
    // if (!params.RowId) params.RowId = 100
    let fd = new FormData();
    for (let key in params) {
        fd.append(key, params[key])
    }
    switch (type.toUpperCase()) {
        case 'POST':
            return http.post(url, fd, exConfig, exData);
            break;
        case 'GET':
            return http.get(url, params, exConfig, exData);
            break;
        case 'DELETE':
            return http.delete(url, params);
            break;
    }
}
// Classify  1 图片 2文件
export default {
    //  上传
    upLoad: data => httpx('/FMS/api/File/FileUpload', 'POST', data, ["file"]),  
    fileDelete: data => httpx('/FMS/api/File/Delete', 'DELETE', data),
    fileQueryList: data => httpx('/FMS/api/Query/list', 'GET', data, ["customError"]),
    devUpLoad: data => httpx('/dev/api/FileUpload', 'POST', data, ["file"]),
    coreUpLoad: data => httpx('/core/api/FileUpload', 'POST', data, ["file"]),
    cpcUpLoad: data => httpx('/CPC/api/FileUpload', 'POST', data, ["file"])
}